import React from "react";
import svg1 from "../images/svg.svg";

const Home = () => {
  return (
    <div className="Home_container">
      <div className="Home_container_box_1">
        <h1>Welcome John!</h1>
        <h3>How to get jobs with top U.S. companies</h3>
        <div className="four_box_row_container">
          <div className="four_box_row">
            <div> 1 </div>
            <div>
              Take 1 <br /> Tests
            </div>
          </div>
          <div className="four_box_row">
            <div> 2 </div>
            <div>
              Pass Live <br /> Coading Challenge
            </div>
          </div>
          <div className="four_box_row">
            <div> 3 </div>
            <div>
              Finalize <br /> Your resume
            </div>
          </div>
          <div className="four_box_row">
            <div> 4 </div>
            <div>
              Receive <br /> job offers
            </div>
          </div>
        </div>
      </div>
      <div className="Home_container_box_2">
        <div className="Home_container_box_2_left_side">
          <img src={svg1} alt="" />
        </div>
        <div className="Home_container_box_2_right_side">
          <h2> Why join </h2>
          <div className="Home_container_box_2_points">
            <div  className="number_points" > 1 </div>
            <div>
              Work full-time at top Silicon Valley or other U.S. companies.
            </div>
          </div>
          <div className="Home_container_box_2_points">
            <div  className="number_points" > 2 </div>
            <div>
              Earn a better salary compared to local companies in your city.
            </div>
          </div>
          <div className="Home_container_box_2_points">
            <div className="number_points" > 3 </div>
            <div>
              Grow as a developer by working with the smartest engineers from
              all over the world.
            </div>
          </div>
          <div className="Home_container_box_2_points">
            <div  className="number_points" > 4 </div>
            <div>
              Get paid monthly directly to your bank account. Forget about
              issues with PayPal or Payoneer.
            </div>
          </div>          
         
        </div>
      </div>
    </div>
  );
};

export default Home;
